.panel-desc {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__inner {
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 50%;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
  &__right {
    width: 50%;
    margin-left: 60px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
  }
}
.panel-advantages {
  padding: 50px 0;
  background: #414141;
  &__inner {
    display: flex;
    align-items: center;
  }
  &__left {
    width: 360px;
    margin-right: 80px;
  }
  &__right {
    width: calc(100% - 440px);
  }
  &__title {
    font-weight: 300;
    font-size: 50px;
    line-height: 120%;
  }
  &__img {
    margin-top: 66px;
    img {
      width: 100%;
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 150%;
  }
  &__list {
    margin-top: 30px;
    li {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      padding-left: 20px;
      position: relative;
      &:not(:first-child) {
        margin-top: 5px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #EBA707;
      }
    }
  }
  &__right-bottom {
    display: flex;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 196px;
    height: 36px;
    background: transparent;
    border: 1px solid #FFFFFF;
    border-radius: 29px;
    color: #fff;
    &:hover {
      background: #EBA707;
      border: 1px solid #EBA707;
    }
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    margin-left: 15px;
  }
}

@media screen and (max-width: 1200px) {
  .panel-advantages {
    &__left {
      margin-right: 40px;
    }
    &__right {
      width: calc(100% - 400px);
    }
  }
}

@media screen and (max-width: 992px) {
  .panel-desc {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
    }
    &__right {
      width: 100%;
      margin: 30px 0 0 0;
    }
  }
  .panel-advantages {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 auto 30px;
      text-align: center;
    }
    &__right {
      width: 100%;
    }
    &__title {
      font-size: 30px;
    }
    &__img {
      max-width: 215px;
      margin: 30px auto 0;
    }
    &__subtitle {
      text-align: center;
    }
  }
}

@media screen and (max-width: 768px) {
  .panel-desc {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__left,
    &__right {
      p {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
  }
  .panel-advantages {
    padding: 30px 0;
    &__subtitle {
      font-size: 16px;
    }
    &__list {
      li {
        font-size: 14px;
        &:not(:first-child) {
          margin-top: 15px;
        }
      }
    }
    &__right-bottom {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
    }
  }
}