.business-card {
  padding: 50px 0;
  &__info {
    font-size: 18px;
    line-height: 120%;
    font-weight: 300;
    margin-top: 15px;
  }
  &__btn {
    font-size: 11px;
    line-height: 120%;
    width: 196px;
    height: 36px;
    border: 1px solid #FFFFFF;
    border-radius: 29px;
    margin-top: 15px;
    background: transparent;
    color: #FFFFFF;
    text-transform: uppercase;
    &:hover {
      background: #EBA707;
      border: 1px solid #EBA707;
    }
  }
}

@media screen and (max-width: 1200px) {
  .business-card {
    &__info {
      font-size: 14px;
    }
  }
}

@media screen and (max-width: 576px) {
  .business-card {
    &__info {
      font-size: 11px;
    }
    &__btn {
      width: 102px;
      height: 20px;
      font-size: 8px;
    }
  }
}