.brandy-advantages {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #373737;
    margin-top: 40px;
    text-align: center;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    color: #565656;
    margin-top: 30px;
    text-align: center;
  }
  &__items {
    display: flex;
    margin-top: 40px;
    &-left {
      width: 50%;
    }
    &-right {
      width: 50%;
      margin-left: 60px;
    }
  }
  &__item {
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    color: #373737;
    position: relative;
    &:before{
      content: '';
      display: inline-block;
      transform: translate(0, 1px);
      width: 6px;
      height: 17px;
      background: #EBA707;
      margin-right: 10px;
    }
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
}
.advantages2 {
  &__subtitle {
    margin-top: 130px;
  }
}
.brandy-info {
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 36px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: #565656;
    margin-top: 40px;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 25px;
  }
  &__item {
    &-title {
      font-weight: 500;
      font-size: 28px;
      line-height: 120%;
      color: #797979;
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
    &-desc {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      margin-top: 8px;
      color: #373737;
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 18px;
    line-height: 180%;
    color: #373737;
    margin-top: 25px;
    text-indent: 30px;
  }
}
.brandy-services {
  background: #414141;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 500;
      font-size: 50px;
      line-height: 120%;
      color: #EBA707;
      position: relative;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 90px;
    margin-top: 50px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 80px;
      height: 80px;
      background: #fff;
      border-radius: 50%;
      flex-shrink: 0;
    }
    &-desc {
      font-weight: 500;
      font-size: 20px;
      line-height: 150%;
      margin-left: 25px;
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
  }
  &__btn {
    width: 196px;
    height: 36px;
    background: transparent;
    color: #fff;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1300px) {
  .brandy-info {
    &__items {
      grid-template-columns: 1fr;
    }
  }
  .brandy-services {
    &__items {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .brandy-services {
    &__items {
      grid-template-columns: 1fr;
      max-width: 800px;
      margin: 50px auto 0;
    }
    &__item {
      justify-content: center;
    }
  }
}

@media screen and (max-width: 992px) {
  .brandy-advantages {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__desc {
      text-align: start;
    }
    &__items {
      display: block;
      &-left {
        width: 100%;
      }
      &-right {
        width: 100%;
        margin: 25px 0 0 0;
      }
    }
  }
  .brandy-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .brandy-services {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .brandy-advantages {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      font-size: 14px;
      margin-top: 20px;
    }
    &__subtitle {
      font-size: 16px;
      margin-top: 15px;
    }
    &__items {
      margin-top: 30px;
    }
    &__item {
      font-size: 14px;
    }
  }
  .brandy-info {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 28px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__subtitle {
      font-size: 14px;
      margin-top: 20px;
    }
    &__item {
      &-title {
        font-size: 28px;
      }
      &-desc {
        font-size: 14px;
      }
    }
    &__desc {
      font-size: 14px;
    }
  }
  .brandy-services {
    padding: 30px 0;
    &__item {
      &-desc {
        font-size: 16px;
      }
    }
    &__bottom {
      flex-direction: column;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
    }
  }
}