.contacts {
  padding: 170px 0 0 0;
  background: #373737;
  &2 {
    padding: 305px 0 135px 0;
  }
  &__inner {
    display: flex;
    align-items: center;
  } &__left {
    width: 50%;
    &-wrapp {
      max-width: 515px;
      margin-left: auto;
      padding: 0 15px;
      img {
        width: 100%;
      }
    }
  }
  &__right {
    width: 50%;
    &-wrapp {
      max-width: 600px;
      padding-left: 70px;
      padding-right: 15px;
      text-align: center;
    }
  }
  .map{
    min-height: 840px;
    width: 100%;
  }
  &__title {
    font-weight: 700;
    font-size: 54px;
    line-height: 120%;
    margin-bottom: 55px;
    span {
      display: block;
      font-size: 39px;
      font-weight: 300;
    }
  }
  &__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
  }
  &__socials {
    display: flex;
    justify-content: center;
    margin: 15px 0 35px 0;
    &-left {
      display: flex;
      align-items: center;
    }
    &-right {
      margin-left: 40px;
    }
  }
  &__social {
    display: flex;
    align-items: center;
    font-size: 16px;
    line-height: 120%;
    &:hover {
      color: #EBA707;
    }
    &-connection {
      margin-left: 20px;
    }
    &-networks {
      display: flex;
      align-items: center;
      margin-bottom: 7px;
    }
    &-messengers {
      display: flex;
      align-items: center;
      margin-left: 20px;
      a {
        &:not(:first-child) {
          margin-left: 11px;
        }
      }
    }
    &-nav,
    &-mail {
      span {
        margin-left: 20px;
      }
    }
    &-mail {
      margin-top: 7px;
    }
  }
  &__info {
    font-size: 18px;
    line-height: 120%;
    margin-top: 11px;
    margin-bottom: 35px;
  }
  &__btns {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
  }
  &__btn {
    border: 1px solid #FFFFFF;
    border-radius: 29px;
    width: 144px;
    height: 36px;
    background: transparent;
    color: #fff;
    &:hover {
      background: #EBA707;
      border: 1px solid #EBA707;
    }
    &:not(:first-child) {
      margin-left: 27px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    &__right {
      &-wrapp {
        padding-left: 30px;
      }
    }
    .map {
      min-height: 700px;
    }
    &__socials {
      display: block;
      &-right {
        margin: 30px 0 0 0;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    padding: 195px 0 0;
    &2 {
      padding: 195px 0 40px;
    }
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
      margin-top: 50px;
      &-wrapp {
        margin:  0 auto;
      }
    }
    &__right {
      width: 100%;
      order: -1;
      &-wrapp {
        padding: 0 15px;
        margin: 0 auto;
      }
    }
    &__socials {
      display: flex;
      &-right {
        margin: 0 0 0 40px;
      }
    }
    .map {
      height: 544px;
    }
  }
}

@media screen and (max-width: 768px) {
  .contacts {
    padding-top: 115px;
    &__title {
      font-size: 58px;
      span {
        font-size: 20px;
        line-height: 120%;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__title {
      margin-bottom: 30px;
    }
    &__socials {
      display: block;
      margin: 25px 0;
      &-right {
        margin: 25px 0 0 0;
      }
    }
    &__social {
      &-nav {
        font-size: 12px;
      }
      &-mail {
        font-size: 12px;
      }
    }
    &__info {
      font-size: 12px;
    }
    &__mode {
      font-size: 16px;
    }
    &__title-btns {
      font-size: 16px;
    }
    &__btns {
      margin-top: 15px;
    }
  }
}