.nav {
  &__list {
    padding: 20px 50px;
    a {
      font-size: 26px;
      line-height: 120%;
      color: #000;
      display: block;
      &:hover {
        color: #EBA707;
      }
      &:not(:first-child) {
        margin-top: 15px;
      }
    }
  }
  &__btn {
    margin-top: 25px;
    background: #000;
    padding: 10px 15px;
    color: #fff;
    font-size: 20px;
    line-height: 120%;
    &:hover {
      background: #EBA707;
    }
  }
}
.select_selected {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 100%;
    width: 13px;
    height: 100%;
  }
}
.display2 {
  h2 {
    font-weight: 300;
    font-size: 60px;
    line-height: 120%;
    position: relative;
    padding-left: 28px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 2px;
      height: calc(100% + 20px);
      transform: translate(0, -50%);
      background: #FFFFFF;
    }
  }
}
.display3 {
  font-weight: 700;
  font-size: 38px;
  line-height: 120%;
}
.display4 {
  font-weight: 700;
  font-size: 60px;
  line-height: 120%;
  text-transform: uppercase;
  position: relative;
  padding-left: 28px;
  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    width: 2px;
    height: calc(100% + 20px);
    transform: translate(0, -50%);
    background: #FFFFFF;
  }
}
.btn-yellow {
  font-size: 11px;
  line-height: 120%;
  background: #EBA707;
  border-radius: 29px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  &:hover {
    background: #000;
    border: 1px solid #EBA707;
  }
}
.btn-gray {
  background: #7A7A7A;
  border-radius: 29px;
  border: 1px solid #FFFFFF;
  font-size: 11px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: #FFFFFF;
  &:hover {
    background: #EBA707;
    border: 1px solid #EBA707;
  }
}
.nice-select {
  background: transparent !important;
  border: none;
  color: #fff;
  display: inline-block;
  width: auto;
  padding: 0;
  &:after {
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 8px;
    height: 4px;
    top: 50%;
    right: -12px;
    margin-top: -2px;
    transform: rotate(0deg)
  }
  .list {
    background: #fff;
    border-radius: 0;
    width: auto;
    min-width: 100%;
    li {
      color: #000;
      padding: 10px;
      font-size: 12px;
    }
  }
  .option.selected {
    font-weight: 400;
  }
}
.nice-select .option:hover, .nice-select .option.focus, .nice-select .option.selected.focus {
  background: rgba(0,0,0,.1);
}
.nice-select.open:after {
  transform: rotate(180deg) ;
}
.modal {
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(20px);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  &__dialog{
    width: 700px;
    max-width: 100%;
    position: relative;
    @media screen and (max-width: 992px) {
      margin: 50px auto;
      max-width: calc(100% - 30px);
    }
  }
  &__close{
    position: absolute;
    z-index: 2;
    left: 100%;
    bottom: 100%;
    transform: translate(6px,-13px);
    background-color: transparent;
  }
  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    &-left {
      width: calc(100% - 250px);
    }
    &-right {
      width: 250px;
      img {
        width: 100%;
      }
    }
    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      color: #EBA707;
      text-align: center;
      margin-bottom: 15px;
    }
  }
  &__form {
    &-group {
      &:not(:first-child) {
        margin-top: 12px;
      }
      input {
        width: 100%;
        height: 30px;
      }
      textarea {
        width: 100%;
        height: 90px;
      }
    }
  }
  @media screen and (min-width: 992px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
}
.form {
  &-group {
    padding: 0;
    margin: 0;
    border: none;
    input {
      font-size: 12px;
      line-height: 120%;
      color: #373737;
      padding: 0 15px;
      &::placeholder {
        color: #C4C4C4;
      }
    }
    textarea {
      font-size: 12px;
      line-height: 120%;
      color: #373737;
      resize: none;
      background: #FFFFFF;
      padding: 5px 15px;
      &::placeholder {
        color: #C4C4C4;
      }
    }
  }
  &-btn {
    width: 160px;
    height: 32px;
    font-weight: 700;
    font-size: 12px;
    line-height: 120%;
    background: #E4E4E4;
    color: #FFFFFF;
    margin: 0 auto;
    &:hover {
      background: #EBA707;
    }
  }
}
.letters-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.letters-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__left {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 7px 20px;
    width: calc(100% - 320px);
  }
  &__right {
    max-width: 320px;
    height: 100%;
    flex-shrink: 0;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    font-weight: 300;
    font-size: 36px;
    line-height: 120%;
    span {
      display: block;
      font-size: 18px;
      line-height: 120%;
      margin-top: 5px;
    }
  }
}
