.offers-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 18px;
}
.offers-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: all 0.5s;
  }
  &:hover {
    .offers-card__title {
      color: #EBA707;
      span {
        color: #fff;
      }
    }
    &:before {
      opacity: 1;
    }
  }
  &__desc {
    position: relative;
    z-index: 4;
  }
  &__img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__icon {
    position: absolute;
    left: 25px;
    bottom: 25px;
    width: 82px;
    height: auto;
    display: flex;
    img {
      width: auto;
      height: auto;
    }
  }
  &__title {
    font-weight: 300;
    font-size: 32px;
    text-align: end;
    position: absolute;
    left: 25px;
    right: 25px;
    bottom: 25px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 700;
      display: block;
      margin-top: 5px;
    }
  }
}
