@media screen and (max-width: 1370px) {
  .offers-card {
    &__title {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 1300px) {
  .letters-card {
    &__left {
      width: calc(100% - 280px);
    }
    &__right {
      max-width: 280px;
    }
    &__title {
      font-size: 28px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .offers-cards {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .display2 {
    h2 {
      font-size: 50px;
    }
  }
  .display4 {
    font-size: 50px;
  }
  .letters-card {
    &__left {
      width: calc(100% - 230px);
    }
    &__right {
      max-width: 230px;
    }
    &__title {
      font-size: 24px;
    }
  }
}

@media screen and (max-width: 992px) {
  .display3 {
    font-size: 28px;
  }
  .offers-card {
    &__icon {
      width: 35px;
      height: auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &__title {
      font-size: 20px;
      span {
        font-size: 16px;
      }
    }
  }
  .display2 {
    h2 {
      font-size: 40px;
    }
  }
  .display4 {
    font-size: 40px;
  }
  .letters-cards {
    grid-template-columns: 1fr;
    max-width: 700px;
    margin: 0 auto;
  }
  .modal {
    &__content {
      flex-direction: column;
      &-title {
        font-size: 26px;
      }
      &-left {
        width: 100%;
      }
      &-right {
        order: -1;
        width: 175px;
      }
    }
    &__close {
      top: 0;
      right: 0;
      left: unset;
      bottom: unset;
    }
  }
}

@media screen and (max-width: 768px) {
  .display2 {
    h2 {
      font-size: 28px;
      padding-left: 15px;
      &:after {
        height: 100%;
      }
    }
  }
  .display3 {
    font-size: 24px;
  }
  .display4 {
    font-size: 28px;
    padding-left: 15px;
  }
  .offers-cards {
    grid-template-columns: 1fr 1fr;
  }
  .letters-card {
    &__left {
      width: 50%;
    }
    &__right {
      width: 50%;
      max-width: 100%;
    }
  }
  .modal {
    &__dialog {
      margin: 150px auto 40px;
    }
    &__close {
      top: -70px;
    }
    &__content {
      &-right {
        position: absolute;
        top: -60px;
        right: 0;
      }
      &-title {
        max-width: 200px;
        text-align: start;
        margin-bottom: 35px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .offers-card {
    &__desc {
      position: absolute;
      top: 60%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      text-align: center;
    }
    &__icon {
      justify-content: center;
      position: static;
      top: unset;
      left: unset;
      width: 70px;
      min-height: 50px;
      margin: 0 auto;
      img {
        width: auto;
      }
    }
    &__title {
      font-size: 18px;
      position: static;
      top: unset;
      left: unset;
      text-align: center;
      padding-top: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      span {
        font-size: 14px;
      }
    }
    &__title {
      br {
        display: none;
      }
    }
  }
  .letters-card {
    &__title {
      font-size: 18px;
      span {
        font-size: 12px;
      }
    }
  }
  .modal {
    &__content-title {
      max-width: 150px;
    }
  }
}

@media screen and (max-width: 500px) {
  .offers-card {
    &__icon {
      width: 50px;
    }
  }
}

@media screen and (max-width: 375px) {
  .offers-cards {
    grid-template-columns: 1fr;
  }
  .offers-card {
    &__icon {
      width: 70px;
    }
  }
  .letters-card {
    &__right {
      img {
        height: auto;
      }
    }
  }
}