.signage-advantages {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 40px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      position: relative;
      color: #373737;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 3px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
}
.signage-price {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 200px;
    margin-right: 80px;
    img {
      width: 100%;
    }
  }
  &__right {
    width: calc(100% - 280px);
  }
  &__title {
    font-weight: 700;
    font-size: 48px;
    line-height: 120%;
    color: #EBA707;
  }
  &__desc {
    font-size: 20px;
    line-height: 130%;
    color: #565656;
    margin-top: 15px;
  }
  &__bottom {
    margin-top: 20px;
    display: flex;
  }
  &__btn {
    width: 277px;
    height: 36px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #7A7A7A;
    margin-left: 30px;
  }
}


@media screen and (max-width: 992px) {
  .signage-advantages {
    &__title {
     h2 {
       font-size: 32px;
     }
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .signage-advantages {
    padding: 15px 0 30px;
    &__title {
      h2 {
        padding-bottom: 0;
        font-weight: 700;
        font-size: 18px;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 30px;
    }
  }
  .signage-price {
    padding: 30px 0;
    &__inner {
      display: block;
    }
    &__left {
      max-width: 115px;
      width: 100%;
      margin: 0 auto 0;
    }
    &__right {
      width: 100%;
      margin-top: 30px;
    }
    &__title {
      font-size: 20px;
      text-align: center;
    }
    &__desc {
      font-size: 14px;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 3px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
    &__bottom {
      flex-direction: column;
      align-items: center;
      margin-top: 30px;
    }
    &__btn {
      width: 197px;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
    }
  }
}