.neon-advantages {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;

      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  &__item {
    display: flex;
    margin: 60px 40px 0 0;
    max-width: 410px;
    &--big {
      max-width: 550px;
    }
    &-number {
      span {
        font-weight: 900;
        font-size: 96px;
        line-height: 90%;
        background: linear-gradient(216.23deg, #B4B4B4 3.13%, #FFFFFF 100.64%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        background-clip: text;
        text-fill-color: transparent;
        display: inline-block;
      }
    }
    &-desc {
      margin-left: 30px;
    }
    &-title {
      font-weight: 900;
      font-size: 36px;
      line-height: 120%;
      color: #797979;
    }
    &-text {
      font-weight: 300;
      font-size: 18px;
      line-height: 150%;
      color: #373737;
      margin-top: 20px;
    }
  }
}
.neon-price {
  background: #414141;
  padding: 50px 0;
  &__inner {
    display: flex;
  }
  &__left {
    width: 275px;
    margin-right: 50px;
    img {
      width: 100%;
    }
  }
  &__right {
    width: calc(100% - 325px);
  }
  &__desc {
    p {
      font-size: 20px;
      line-height: 150%;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__bottom {
    margin-top: 30px;
    display: flex;
  }
  &__btn {
    width: 277px;
    height: 36px;
    background: transparent;
    &:hover {
      border: 1px solid #EBA707;
    }
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #7A7A7A;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1400px) {
  .neon-advantages {
    &__items {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 60px 40px;
      max-width: 1000px;
      margin: 40px auto 0;
    }
    &__item {
      margin: 0;
      max-width: 100%;
    }
  }
}

@media screen and (max-width: 992px) {
  .neon-advantages {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__item-title {
      font-size: 24px;
    }
  }
  .neon-price {
    &__left {
      width: 200px;
    }
    &__right {
      width: calc(100% - 250px);
    }
  }
}

@media screen and (max-width: 768px) {
  .neon-advantages {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-size: 22px;
        &:after {
          display: none;
        }
      }
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 40px;
    }
    &__item {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        top: -20px;
        transform: translate(-50%, 0);
        width: 100%;
        height: 1px;
        background: #C7C7C7;
      }
      &-number {
        font-size: 80px;
      }
      &-desc {
        margin-left: 20px;
      }
      &-text {
        font-size: 14px;
      }
    }
  }
  .neon-price {
    padding: 30px 0;
    &__inner {
      display: block;
    }
    &__left {
      width: 275px;
      margin: 0 auto;
    }
    &__right {
      width: 100%;
      margin-top: 30px;
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
    &__bottom {
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
    &__info {
      margin: 10px 0 0 0;
      font-size: 14px;
    }
  }
}