.pylons {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
}
.pylons-info {
  background: #414141;
  padding: 50px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 500;
      font-size: 50px;
      line-height: 120%;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #EBA707;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 180%;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 25px;
      }
    }
  }
  &__subtitle {
    font-size: 24px;
    line-height: 120%;
    margin-top: 25px;
    text-align: center;
    color: #EBA707;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 15px;
  }
  &__item {
    &-title {
      font-weight: 500;
      font-size: 28px;
      line-height: 120%;
      position: relative;
      &:before {
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
    }
    &-desc {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      margin-top: 8px;
    }
  }
}
.pylons-info2 {
  padding: 50px 0;
  &__inner {
    display: flex;
  }
  &__left {
    width: 200px;
    margin-right: 80px;
    img {
      width: 100%;
    }
  }
  &__right {
    width: calc(100% - 280px);
  }
  &__desc {
    font-size: 20px;
    line-height: 200%;
    color: #575757;
  }
  &__bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
    margin-left: 30px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #7A7A7A;
  }
}

@media screen and (max-width: 1200px) {
  .pylons-info {
    &__items {
      grid-template-columns: 1fr 1fr;
      max-width: 800px;
      margin: 15px auto 0;
    }
  }
}

@media screen and (max-width: 992px) {
  .pylons {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .pylons-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
  }
  .pylons-info2 {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      max-width: 200px;
      margin: 0 auto;
    }
    &__right {
      width: 100%;
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 768px) {
  .pylons {
    padding: 15px 0 30px 0;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 20px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
  }
  .pylons-info {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 26px;
        font-weight: 700;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      margin-top: 20px;
      p {
        font-size: 14px;
      }
    }
    &__subtitle {
      font-size: 20px;
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__item {
      text-align: center;
      &-title {
        font-size: 22px;
      }
      &-desc {
        font-size: 14px;
      }
    }
  }
  .pylons-info2 {
    padding: 30px 0;
    &__desc {
      font-size: 14px;
    }
    &__bottom {
      flex-direction: column;
      margin-top: 30px;
      text-align: center;
    }
    &__btn {
      margin: 0 0 10px 0;
      order: -1;
    }
  }
}