.mistake {
  padding: 320px 0 150px;
  background: #EBA707;
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__img {
    img {
      width: 100%;
    }
  }
  &__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
  }
}

@media screen and (max-width: 992px) {
  .mistake {
    padding: 200px 0 50px;
    &__title {
      font-size: 20px;
    }
  }
}

@media screen and (max-width: 768px) {
  .mistake {
    padding: 120px 0 50px;
  }
}