.showcases-info {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-top: 30px;
  }
  &__desc-left {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #373737;
  }
  &__desc-right {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #373737;
  }
}
.showcases-desc {
  background: #414141;
  padding: 50px 0;
  &__inner {
    text-align: center;
  }
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 36px;
      line-height: 120%;
      color: #EBA707;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 39px;
    margin-top: 40px;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 24px;
    line-height: 120%;
    color: #EBA707;
    margin-top: 20px;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px;
    margin-top: 20px;
  }
  &__item {
    font-weight: 300;
    font-size: 20px;
    line-height: 120%;
  }
}
.showcases-info2 {
  padding: 50px 0;
  &__inner {
    display: flex;
  }
  &__left {
    width: 200px;
    margin-right: 80px;
    img {
      width: 100%;
    }
  }
  &__right {
    width: calc(100% - 280px);
  }
  &__desc {
    font-size: 20px;
    line-height: 200%;
    color: #575757;
  }
  &__bottom {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 30px;
  }
  &__btn {
    width: 277px;
    height: 36px;
    margin-left: 30px;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: #7A7A7A;
  }
}

@media screen and (max-width: 1200px) {
  .showcases-desc {
    &__items {
      grid-template-columns: 1fr 1fr;
      max-width: 800px;
      margin: 20px auto;
    }
  }
}

@media screen and (max-width: 992px) {
  .showcases-info {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__desc {
      grid-template-columns: 1fr;
      gap: 30px;
    }
  }
  .showcases-info2 {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      max-width: 200px;
      margin: 0 auto;
    }
    &__right {
      width: 100%;
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 768px) {
  .showcases-info {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 20px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc-left {
      font-size: 14px;
    }
    &__desc-right {
      font-size: 14px;
    }
  }
  .showcases-desc {
    padding: 30px 0;
    &__title {
      h2 {
        font-size: 26px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__text {
      font-size: 14px;
      margin-top: 30px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__items {
      grid-template-columns: 1fr;
      gap: 15px;
    }
    &__item {
      font-size: 16px;
    }
  }
  .showcases-info2 {
    padding: 30px 0;
    &__desc {
      font-size: 14px;
    }
    &__bottom {
      flex-direction: column;
      margin-top: 30px;
      text-align: center;
    }
    &__btn {
      margin: 0 0 10px 0;
      order: -1;
    }
  }
}