.calendars {
  padding: 25px 0 50px;
  color: #151515;
  &__title {
    font-weight: 300;
    font-size: 44px;
    line-height: 120%;
    text-align: center;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    margin: 30px auto 0;
    max-width: 800px;
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    &-left {
      width: 50%;
      p {
        font-weight: 300;
        font-size: 20px;
        line-height: 150%;
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
        span {
          font-size: 24px;
          font-weight: 700;
        }
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
    &-right {
      width: 50%;
      margin-left: 60px;
      p {
        font-weight: 300;
        font-size: 20px;
        line-height: 150%;
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
        span {
          font-size: 24px;
          font-weight: 700;
        }
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
  }
}
.calendars-services {
  background: #4D4D4D;
  padding: 50px 0;
  &__title {
    text-align: center;
    font-size: 44px;
    line-height: 120%;
  }
  &__desc  {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    &-left {
      width: 50%;
      p {
        font-weight: 300;
        font-size: 20px;
        line-height: 130%;
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
        span {
          font-size: 24px;
          font-weight: 700;
        }
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
    &-right {
      width: 50%;
      margin-left: 60px;
      p {
        font-weight: 300;
        font-size: 20px;
        line-height: 130%;
        padding-left: 20px;
        position: relative;
        &:after {
          content: '';
          position: absolute;
          top: 7px;
          left: 0;
          width: 6px;
          height: 6px;
          background: #EBA707;
        }
        span {
          font-size: 24px;
          font-weight: 700;
        }
        &:not(:first-child) {
          margin-top: 30px;
        }
      }
    }
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 30px;
  }
  &__btn {
    width: 196px;
    height: 35px;
    border: 1px solid #FFFFFF;
    border-radius: 29px;
    font-size: 11px;
    line-height: 26px;
    letter-spacing: 0.12em;
    text-transform: uppercase;
    background: transparent;
    color: #FFFFFF;
    &:hover {
      background: #EBA707;
      border: 1px solid #EBA707;
    }
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    margin-left: 30px;
  }
}

@media screen and (max-width: 992px) {
  .calendars {
    &__title {
      font-size: 32px;
    }
    &__desc {
      display: block;
      &-left {
        width: 100%;
      }
      &-right {
        width: 100%;
        margin: 30px 0 0 0;
      }
    }
  }
  .calendars-services {
    padding: 50px 0 0 0;
    &__inner {
      padding-bottom: 30px;
    }
    &__title {
      font-size: 32px;
    }
    &__desc {
      display: block;
      &-left {
        width: 100%;
      }
      &-right {
        width: 100%;
        margin: 30px 0 0 0;
      }
    }
    &__bottom {
      flex-direction: column;
      background: #606060;
      margin-top: 0;
      padding: 30px 0;
    }
    &__info {
      margin: 10px 0 0 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .calendars {
    padding: 15px 0 30px;
    &__title {
      font-size: 28px;
    }
    &__subtitle {
      font-size: 14px;
    }
    &__desc {
      &-left {
        p {
          padding-left: 15px;
          font-size: 14px;
          &:after {
            top: 5px;
          }
          span {
            font-size: 14px;
          }
        }
      }
      &-right {
        p {
          padding-left: 15px;
          font-size: 14px;
          &:after {
            top: 5px;
          }
          span {
            font-size: 14px;
          }
        }
      }
    }
  }
  .calendars-services {
    padding: 30px 0 0 0;
    &__title {
      font-size: 28px;
    }
    &__desc {
      &-left {
        p {
          padding-left: 15px;
          font-size: 14px;
          &:after {
            top: 5px;
          }
        }
      }
      &-right {
        p {
          padding-left: 15px;
          font-size: 14px;
          &:after {
            top: 5px;
          }
        }
      }
    }
  }
}