.letters {
  padding: 230px 0 60px;
  background: #282828;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    max-width: 660px;
    margin-left: 50px;
  }
  &__icon {
    width: 132px;
    height: auto;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-left: 60px;
  }
  &__desc {
    p {
      font-size: 20px;
      line-height: 140%;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
}
.letters-content {
  padding: 50px 0 25px;
}
.letters-info {
  padding: 25px 0 50px;
  color: #565656;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
   width: calc(100% - 710px);
  }
  &__right {
    width: 650px;
    margin-left: 60px;
  }
  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 120%;
  }
  &__list {
    margin-top: 30px;
    li {
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      padding-left: 21px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #EBA707;
      }
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__list2 {
    margin-top: 30px;
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-top: 30px;
    }
    &-icon {
      width: 64px;
      height: 64px;
      background: #565656;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-shrink: 0;
      &2 {
        background: radial-gradient(50% 50% at 50% 50%, #A2A1A1 0%, #565656 100%);
      }
      &3 {
      background: radial-gradient(50% 50% at 50% 50%, #A2A1A1 0%, #565656 100%);
    }
    }
    &-desc {
      margin-left: 28px;
    }
    &-title {
      font-size: 24px;
      line-height: 120%;
      font-weight: 500;
    }
    &-text {
      font-size: 18px;
      line-height: 120%;
      margin-top: 10px;
    }
  }
}
.advantages2 {
  &__inner {
    display: flex;
  }
  &__left {
    width: 480px;
    flex-shrink: 0;
    background: #343434;
    padding: 180px 30px 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      width: 5000px;
      height: 100%;
      background: #343434;
    }
  }
  &__right {
    display: flex;
    background: #575757;
    padding: 100px 60px;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      width: 5000px;
      height: 100%;
      background: #575757;
    }
  }
  &__title {
    position: relative;
    z-index: 2;
    span:nth-child(1) {
      font-weight: 900;
      font-size: 100px;
      line-height: 80%;
    }
    span:nth-child(2) {
      position: absolute;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-weight: 900;
      font-size: 400px;
      color: #EBA707;
      pointer-events: none;
    }
  }
  &__subtitle {
    font-size: 35px;
    line-height: 120%;
    margin-top: 90px;
    max-width: 320px;
  }
  &__items {
    display: flex;
    justify-content: space-between;
    &-left {
      width: 50%;
    }
    &-right {
      width: 50%;
      margin-left: 60px;
    }
  }
  &__item {
    display: flex;
    &:not(:first-child) {
      margin-top: 50px;
    }
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 80%;
      color: #6B6B6B;
      display: flex;
      flex-direction: column;
      justify-content: start;
    }
    &-desc {
      font-weight: 500;
      font-size: 20px;
      line-height: 120%;
      margin-left: 15px;
    }
  }
}
.application {
 &__inner {
   display: flex;
   justify-content: center;
   align-items: center;
   background: #606060;
   padding: 30px 15px;
 }
  &__btn {
    width: 196px;
    height: 36px;
    background: transparent;
  }
  &__desc {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    margin-left: 17px;
  }
}
.more-services {
  background: #282828;
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__slider {
    width: calc(100% - 350px);
  }
  &__desc {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: end;
    padding: 40px 40px 0 40px;
    width: 350px;
    flex-shrink: 0;
  }
  &__title {
    font-size: 72px;
    line-height: 120%;
    text-transform: uppercase;
    color: #6D6D6D;
  }
  .swiper-button__btns {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    margin-top: 15px;
    padding-bottom: 30px;
  }
  .swiper-button-prev {
    display: flex;
    align-items: center;
    left: 50px;
    &:after {
      display: none;
    }
    svg {
      width: 29px;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #C7C7C7;
      padding-left: 12px;
    }
  }
  .swiper-button-next {
    display: flex;
    align-items: center;
    right: 50px;
    &:after {
      display: none;
    }
    svg {
      width: 29px;
    }
    span {
      font-size: 14px;
      line-height: 120%;
      color: #C7C7C7;
      padding-right: 12px;
    }
  }
}
.letters-desc {
  padding: 50px 0;
  &__item {
    position: relative;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(31, 35, 73, 0.79);
      backdrop-filter: blur(9px);
    }
    &2 {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(131, 73, 27, 0.79);
      }
    }
    &3 {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(31, 31, 40, 0.79);
      }
    }
    &4 {
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(61, 42, 30, 0.79);
      }
    }
    &-content {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      padding: 25px;

      height: 100%;
      //&2 {
      //  background: rgba(131, 73, 27, 0.79);
      //}
      //&3 {
      //  background: rgba(31, 31, 40, 0.79);
      //}
      //&4 {
      //  background: rgba(61, 42, 30, 0.79);
      //}
      p {
        font-weight: 300;
        font-size: 16px;
        line-height: 140%;
        &:not(:first-child) {
          margin-top: 20px;
        }
      }
    }
    &-text{
      height: 100%;
      overflow: hidden;
    }
  }

}
.letters-desc2 {
  padding:0 0 50px 0;
  color: #565656;
  display: none;
  &__items {
    display: grid;
    grid-template-columns:1fr;
    gap: 60px;
  }
  &__item {
    &-title {
      font-weight: 700;
      font-size: 32px;
    }
    &-desc {
      margin-top: 30px;
      p {
        font-size: 20px;
        line-height: 140%;
        position: relative;
        &:not(:first-child) {
          margin-top: 30px;
        }
        &:before{
          content: '';
          display: inline-block;
          transform: translate(0, 3px);
          width: 6px;
          height: 17px;
          background: #EBA707;
          margin-right: 10px;
        }
      }
    }
  }
}
.letters-info2 {
  padding: 50px 0;
  color: #565656;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
    margin-left: 60px;
  }
  &__list {
    li {
      font-weight: 300;
      font-size: 18px;
      line-height: 180%;
      padding-left: 21px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 6px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #EBA707;
      }
      &:not(:first-child) {
        margin-top: 10px;
      }
    }
  }
  &__bottom {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap:30px 115px;
    padding: 0 50px;
    margin-top: 40px;
  }
  &__item {
    text-align: center;
    &-icon {
      max-width: 70px;
      height: auto;
      margin: 0 auto;
      img {
        width: auto;
      }
    }
    &-desc {
      font-size: 18px;
      line-height: 120%;
      margin-top: 10px;
    }
  }
}
.letters-content2 {
  padding: 0 0 50px;
}
.letters__gallery {
  padding: 50px 0 25px;
  &-inner {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px;
  }
  &-img {
    overflow: hidden;
    &:hover {
     img {
       transform: scale(1.2);
     }
    }
    img {
      width: 100%;
      transition: all 0.5s;
    }
  }
}
.letters-info3 {
  padding: 25px 0 50px;
}
.letters-services2 {
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
    margin-left: 60px;
  }
  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: #EBA707;
  }
  &__list {
    margin-top: 40px;
    li {
      font-size: 20px;
      line-height: 120%;
      color: #565656;
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__btn {
    width: 277px;
    height: 36px;
    margin-top: 25px;
  }
}

