.advertisement-desc {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    font-weight: 300;
    font-size: 20px;
    line-height: 180%;
    color: #373737;
    margin-top: 30px;
    text-align: center;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 130%;
    color: #565656;
    margin-top: 30px;
    text-align: center;
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 30px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-img {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      background: #EBA707;
      border-radius: 50%;
    }
    &-desc {
      font-weight: 300;
      font-size: 24px;
      line-height: 120%;
      color: #565656;
      margin-left: 20px;
    }
  }
}

@media screen and (max-width: 1200px) {
  .advertisement-desc {
    &__items {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 992px) {
  .advertisement-desc {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      grid-template-columns: 1fr;
    }
  }
}

@media screen and (max-width: 768px) {
  .advertisement-desc {
    padding: 30px 0;
    &__title {
      h2 {
        font-weight: 700;
        font-size: 20px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      font-size: 14px;
      margin-top: 20px;
      text-align: start;
    }
    &__subtitle {
      font-size: 18px;
      margin-top: 20px;
    }
    &__items {
      gap: 15px;
    }
    &__item {
      &-desc {
        font-size: 16px;
      }
    }
  }
}