.roofs {
  padding: 25px 0 50px;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    h2 {
      font-weight: 300;
      font-size: 44px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__desc {
    margin-top: 30px;
    p {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      color: #373737;
      position: relative;
      &:before{
        content: '';
        display: inline-block;
        transform: translate(0, 1px);
        width: 6px;
        height: 17px;
        background: #EBA707;
        margin-right: 10px;
      }
      &:not(:first-child) {
        margin-top: 30px;
      }
    }
  }
  &__subtitle {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-top: 30px;
    h3 {
      font-weight: 300;
      font-size: 36px;
      line-height: 120%;
      color: #565656;
      padding-bottom: 12px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 0);
        width: calc(100% - 20px);
        height: 2px;
        background: #C7C7C7;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 60px;
    margin-top: 30px;
  }
  &__item {
    display: flex;
    align-items: center;
    &-number {
      font-weight: 900;
      font-size: 96px;
      line-height: 90%;
      color: #EBA707;
      display: flex;
      align-items: start;
    }
    &-desc {
      font-weight: 300;
      font-size: 26px;
      line-height: 120%;
      color: #373737;
      margin-left: 20px;
    }
  }
}
.roofs-advantages {
  background: #414141;
  padding: 60px 0;
  &__title {
    display: flex;
    justify-content: center;
    align-items: center;
   span {
     &:nth-child(1) {
       font-weight: 500;
       font-size: 200px;
       line-height: 90%;
       color: #6D6D6D;
     }
     &:nth-child(2) {
       font-weight: 500;
       font-size: 44px;
       line-height: 130%;
       margin-left: 20px;
     }
   }
  }
  &__desc {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 25px;
    margin-top: 25px;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 28px;
    line-height: 120%
  }
  &__list {
    margin-top: 30px;
    li {
      font-weight: 300;
      font-size: 20px;
      line-height: 150%;
      padding-left: 24px;
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 0;
        width: 6px;
        height: 6px;
        background: #EBA707;
      }
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__text {
    font-weight: 300;
    font-size: 20px;
    line-height: 150%;
    margin-top: 25px;
  }
  &__bottom {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
  }
  &__btn {
    width: 196px;
    height: 36px;
    border: 1px solid #FFFFFF;
    border-radius: 29px;
    background: transparent;
    color: #fff;
  }
  &__info {
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    margin-left: 30px;
  }
}

@media screen and (max-width: 992px) {
  .roofs {
    &__title {
      h2 {
        font-size: 32px;
      }
    }
    &__items {
      gap: 30px;
    }
    &__item {
      flex-direction: column;
      text-align: center;
      &-number {
        font-size: 60px;
      }
      &-desc {
        margin: 10px 0 0 0;
      }
    }
  }
  .roofs-advantages {
    &__title {
      span {
        &:nth-child(2) {
          font-size: 32px;
        }
      }
    }
    &__desc {
      grid-template-columns: 1fr;
    }
    &__subtitle {
      font-size: 24px;
      color: #EBA707;
    }
  }
}

@media screen and (max-width: 768px) {
  .roofs {
    padding: 15px 0 30px;
    &__title {
      h2 {
        font-size: 18px;
        font-weight: 700;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__desc {
      p {
        font-size: 14px;
      }
    }
    &__subtitle {
      h3 {
        font-size: 18px;
        padding-bottom: 0;
        &:after {
          display: none;
        }
      }
    }
    &__items {
      gap: 10px;
      margin-top: 15px;
    }
    &__item {
      &-desc {
        font-size: 14px;
      }
    }
  }
  .roofs-advantages {
    padding: 30px 0;
    &__title {
      span:nth-child(2) {
        font-size: 26px;
      }
    }
    &__subtitle {
      font-size: 18px;
    }
    &__list {
      margin-top: 15px;
      li {
        font-size: 14px;
        &:after {
          top: 6px;
        }
        &:not(:first-child) {
          margin-top: 5px;
        }
      }
    }
    &__text {
      font-size: 14px;
      margin-top: 15px;
    }
    &__bottom {
      flex-direction: column;
    }
    &__info {
      font-size: 14px;
      margin: 10px 0 0 0;
    }
  }
}