.portfolio {
  padding: 170px 0 0px;
  &__inner {
    display: flex;
    justify-content: space-between;
    position: relative;
  }
  &__left {
    width: calc(100% - 420px);
    &-wrapp {
      background: #F0F0F0;
      padding: 65px 60px 40px;
      position: relative;
      margin: 40px 0;
      &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 29px;
        background: #EBA707;
      }
    }
  }
  &__right {
    text-align: center;
    width: 370px;
    margin-left: 50px;
    background: #434343;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: start;
    &-wrapp {
      position: fixed;
      padding: 15px 30px;
    }
    &-top {
      text-align: center;
    }
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      width: 5000px;
      height: 100%;
      background: #434343;
    }
  }
  .nice-select {
    &:after {
      z-index: 0;
    }
  }
  &__list {
    text-align: center;
  }
  &__btn {
    position: absolute;
    top: 55px;
    right: 20px;
  }
  &__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 120%;
    color: #373737;
  }
  &__desc {
    font-size: 18px;
    line-height: 120%;
    color: #373737;
    margin-top: 20px;
  }
  &__gallery {
    &-img {
      display: block;
      img {
        height: 100%;
        width: 100%;
      }
      &--big {
        grid-row: 1/3;
        grid-column: 1/2;
      }
    }
    &1 {
      display: grid;
      grid-template-columns: 1.5fr 1fr;
      gap: 4px;
      margin-top: 25px;
      .portfolio__gallery-img {

        &:nth-child(3) ~ .portfolio__gallery-img {
          display: none;
        }
      }
    }
    &2 {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 4px;
      margin-top: 80px;
      .portfolio__gallery-img {
        &:nth-child(2) ~ .portfolio__gallery-img {
          display: none;
        }
      }
    }
    &3 {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 4px;
      margin-top: 80px;
      .portfolio__gallery-img {
        &:nth-child(4) ~ .portfolio__gallery-img {
          display: none;
        }
      }
    }
    &4 {
      margin-top: 80px;
      &-top {
        display: grid;
        grid-template-columns: 1.5fr 1fr;
        gap: 4px;
      }
      &-bottom {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 4px;
        margin-top: 4px;
        .portfolio__gallery-img {
          &:nth-child(2) ~ .portfolio__gallery-img {
            display: none;
          }
        }
      }
    }
  }
  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 120%;
    color: #797979;
  }
  &__nav-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    margin-top: 25px;
    text-transform: uppercase;
    margin-bottom: 30px;
   span {
     position: relative;
     padding-bottom: 5px;
     &:after {
       content: '';
       position: absolute;
       left: 50%;
       bottom: 0;
       transform: translate(-50%, 0);
       width: 60%;
       height: 2px;
       background: #EBA707;
     }
   }
  }
  &__link {
    font-size: 14px;
    line-height: 120%;
    display: block;
    &:hover {
      color: #EBA707;
    }
    &:not(:first-child) {
      margin-top: 20px;
    }
  }
  &__social {
    position: relative;
    margin-top: 170px;
    &-img {
      position: absolute;
      z-index: 0;
      left: 50%;
      bottom: 0;
      transform: translate(-50%, 0);
      width: 100%;
      pointer-events: none;
    }
    &-desc {
      position: relative;
      z-index: 2;
    }
    &-title {
      font-weight: 300;
      font-size: 38px;
      line-height: 120%;
      text-align: center;
      span {
        display: block;
        font-size: 25px;
      }
    }
    &-btn {
      width: 166.19px;
      height: 33.67px;
      border: 1px solid #FFFFFF;
      border-radius: 29px;
      margin: 15px auto 0;
      font-weight: 700;
      font-size: 10px;
      line-height: 120%;
      background: transparent;
      color: #fff;
    }
  }
  &__nav-mob {
    justify-content: space-between;
    align-items: center;
    display: none;
  }
  &__mob-subtitle {
    font-size: 14px;
    line-height: 90%;
    color: #373737;
  }
}


@media screen and (max-width: 1200px) {
  .portfolio {
    .container {
      padding: 0;
    }
    &__inner {
     flex-direction: column;
    }
    &__left {
      width: 100%;
      padding: 15px;
      &-wrapp {
        padding: 25px 20px;
        margin: 0;
        &:after {
          display: none;
        }
      }
    }
    &__right {
      width: 100%;
      margin: 0;
      order: -1;
      &-wrapp {
        position: static;
        padding: 0;
        display: flex;
        flex-direction: column;
      }
      &:after {
        display: none;
      }
      &-top {
        background: #1C1C1C;
        padding: 15px;
      }
    }
    &__title {
      color: #fff;
    }
    &__nav-title {
      display: none;
    }
    &__list {
      display: none;
    }
    &__social {
      width: 100%;
      margin-top: 0;
      padding: 15px;
      &-img {
        width: 210px;
      }
    }
    &__nav-mob {
      display: flex;
      order: 3;
      background: #EBA707;
      padding: 10px;
    }
  }
}

@media screen and (max-width: 992px ) {
  .portfolio {
    padding: 150px 0 0;
  }
}

@media screen and (max-width: 768px) {
  .portfolio {
    padding: 70px 0 0 0;
    &__title {
      font-size: 30px;
    }
    &__social {
      &-title {
        font-size: 28px;
        span {
          font-size: 20px;
        }
      }
    }
    &__subtitle {
      font-size: 18px;
    }
    &__desc {
      font-size: 14px;
      margin-top: 10px;
    }
  }
}

@media screen and (max-width: 576px) {
  .portfolio {
    &__gallery {
      grid-template-columns: 1fr 1fr;
      &-img {
        &--big {
          grid-column: 1/3;
          grid-row: 1/3;
        }
      }
      &4 {
        &-top {
          grid-template-columns: 1fr 1fr;
        }
      }
    }
  }
}