.leaflets {
  padding: 50px 0;
  &__card {
    &-price {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-top: 15px;
    }
    &-info {
      font-weight: 300;
      font-size: 18px;
      line-height: 120%;
      margin-top: 30px;
    }
    &-btn {
      width: 196px;
      height: 36px;
      font-size: 11px;
      line-height: 120%;
      border: 1px solid #FFFFFF;
      border-radius: 29px;
      text-transform: uppercase;
      color: #FFFFFF;
      margin-top: 4px;
      background: transparent;
      &:hover {
        background: #EBA707;
        border: 1px solid #EBA707;
      }
    }
  }
  &__price {
    &-left {
      font-weight: 700;
      font-size: 55px;
      line-height: 120%;
      position: relative;
      span {
        position: absolute;
        top: 90%;
        left: 50%;
        width: calc(100% + 20%);
        transform: translate(-50%, 0);
        font-size: 11px;
        line-height: 120%;
        display: block;
      }
    }
    &-center {
      font-weight: 700;
      font-size: 55px;
      line-height: 120%;
      margin: 0 10px;
    }
    &-right {
      font-weight: 700;
      font-size: 55px;
      line-height: 120%;
      position: relative;
      span {
        position: absolute;
        top: 90%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, 0);
        font-size: 11px;
        line-height: 120%;
        display: block;
      }
    }
  }
}

@media screen and (max-width: 1300px) {
  .leaflets {
    &__price {
      &-left {
        font-size: 28px;
        span {
          width: 230%;
        }
      }
      &-center {
        font-size: 28px;
        margin: 0 4px;
      }
      &-right {
        font-size: 28px;
        span {
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .leaflets {
    padding: 25px 0;
  }
}

@media screen and (max-width: 576px) {
  .leaflets {
    &__card {
      &-price {
        margin-top: 7px;
      }
      &-info {
        font-size: 10px;
      }
      &-btn {
        width: 102px;
        height: 20px;
        font-size: 8px;
      }
    }
  }
}